<template>
  <div>视频接口301</div>
</template>

<script>
import { videoTagList, videoCategory, getTagVideo, geAllVideo } from '@/network/video'
export default {
  created() {
    this.init()
  },
  methods: {
    init() {
      this.videoTagList()
      this.videoCategory()
      this.geAllVideo()
    },
    // 获得视频标签
    videoTagList() {
      videoTagList().then(res => {
        console.log(res)
      })
    },
    // 获得视频分类
    videoCategory() {
      videoCategory().then(res => {
        console.log(res)
      })
    },
    // 获得全部视频
    geAllVideo(){
      geAllVideo().then(
        res=>{
          console.log(res);
        }
      )
    }
  }
}
</script>

<style></style>
